.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}


















































































































/* 动画 */
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity 5s;
  transition: opacity 5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
/* 提示弹窗 */
.popUps {
  /*position: fixed;*/
  /*top: 0;*/
  /*left: 0;*/
  /*bottom: 0;*/
  /*right: 0;*/
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
  background: rgba(55, 58, 71, 0.9);
  /*opacity: 0;*/
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
}
.popUps > div {
  width: 50%;
  max-width: 560px;
  min-width: 290px;
  background: #fff;
  padding: 4em;
  text-align: center;
  position: absolute;
  z-index: 5;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
/* select及其下方的接口宽度样式 */
.swagger-left {
  width: 21%;
  margin-top: 0px;
  position: fixed;
  height: 100%;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
/* 单选框 */
.form-control {
  display: block;
  width: 80%;
  margin: 0 auto 10px;
  padding: 4px 6px;
  height: 36px;
}
.nav-list {
  margin: 0;
  padding: 0;
}
.nav-list > li {
  display: block;
  margin: 0;
  border: 0;
  height: 56px;
  line-height: 56px;
  position: relative;
  border-left: 5px solid #fff;
  cursor: pointer;
  text-align: left;
  padding:0 0 0 10%;
}
.nav-list > li:hover, .nav-list > li.active {
  background-color: #F3F8E4;
  color: #8ABF00;
  border-left: 5px solid #8ABF00;
}
/* 第一层接口列表名字 */
.navList-name {
  margin-right: 14px;
}
.navList-description {
  padding-right: 40px;
  width: 100px;
  display: inline-block;
  text-align: left;
}
.navList-number {
  position: absolute;
  right: 27px;
  top: 16px;
  width: 20px;
  height: 20px;
  background-color: #FF3C43;
  border-radius: 10px;
  text-align: center;
  line-height: 20px;
  color: #fff;
  font-size: 14px;
}
/* 接口列表 */
.swagger-category {
  padding: 0;
  margin-left: 21%;
  width: 22%;
  margin-top: 0px;
  position: fixed;
  /* background: #337ab7; */
  height: 100%;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.swagger-category .categoryLi {
  text-align: left;
  margin-bottom: 5px;
  padding: 5px 10px;
}
.categoryLi .categoryLi-type {
  height: 20px;
  line-height: 20px;
}
.categoryLi .categoryLi-path{
  padding: 2px 4px;
  font-size: 90%;
  color: #c7254e;
  white-space: nowrap;
  background-color: #f9f2f4;
  border-radius: 4px;
}
.categoryLi .categoryLi-name {
  display: block;
  height: 20px;
  line-height: 20px;
}


























































































































































































































































































































































































































































































/* 响应参数说明部分 */
.ResponseParameter .head {
  font-size: 16px;
  font-weight: 700;
  background-color: #F8F8F8;
}
.ResponseParameter > ul {
  overflow: hidden;
  border: 1px solid #ddd;
}
.ResponseParameter > ul li {
  overflow: hidden;
  border-bottom: 1px solid #ddd;
}
.ResponseParameter > ul li:last-child {
  border-bottom: 0;
}
.ResponseParameter > ul li > span {
  width: 30%;
  float: left;
  padding: 8px 4px;
  border-right: 1px solid #ddd;
}
.ResponseParameter > ul li > span:last-child {
  border-right: 0;
}
/* 调试结果区域样式 */
.debugging-result {
  text-align: left;
  font-size: 0;
}
.debugging-result > span {
  font-size: 14px;
  display: inline-block;
  padding: 10px 15px;
  border: 1px solid #EBEBEB;
  position: relative;
  border-right: 0;
  bottom: -1px;
}
.debugging-result > span:last-of-type {
  border-right: 1px solid #EBEBEB;
}
.debugging-result > span.active {
  -webkit-box-shadow: 0 3px #89BF05 inset;
          box-shadow: 0 3px #89BF05 inset;
  color: #89BF05;
  border-bottom: 1px solid transparent;
  border-bottom: 1px solid #FFFFFF;
}
.debugging-curl > div {
  color: #393939;
  font-size: 13px;
  white-space: nowrap;
  overflow-x: auto;
  padding: 15px;
}
.result-content {
  border-top: 1px solid #EBEBEB;
  padding-top: 15px;
}
.result-content > div {
  border: 1px solid #ddd;
  min-height: 210px;
  font-size: 16px;
  text-align: left;
  padding: 40px 15px 15px;
}
.debugging-header > ul {
  border: 1px solid #ddd;
}
.debugging-header li {
  border-bottom: 1px solid #ddd;
}
.debugging-header li > span {
  padding: 10px 15px 10px 5px;
  display: inline-block;
}
.debugging-header li > span:first-child {
  font-weight: 700;
  width: 38%;
  border-right: 1px solid #ddd;
}
.debugging-header li > span:last-child {
  width: 45%;
}
.debugging-header .head {
  font-weight: 700;
}
/* 请求参数表格 */
.table-tr {
  border-bottom: 1px solid #ddd;
  overflow: hidden;
  word-wrap: break-word;
}
.table-head {
  font-size: 16px;
  font-weight: 700;
  background-color: #F8F8F8;
}
.table-head .table-td {
  padding: 8px 4px;
}
.table-td {
  border-right: 1px solid #ddd;
  width: 15%;
  float: left;
  padding: 8px 4px;
  /*padding-bottom: 1000px;*/
  /*margin-bottom: -1000px;*/
  text-align: left;
}
.table-td:last-child {
  border-right: 0;
}
/* 调试：附带参数列表 */
.content-parameter {
  margin-bottom: 45px;
  border-top: 1px solid #ddd;
}
.content-parameter li {
  font-weight: 700;
  border-bottom: 1px solid #ddd;
  overflow: hidden;
}
.content-parameter .parameter-head {
  overflow: hidden;
}
/* 首行单独样式 */
.parameter-head input {
  width: 5%;
  float: left;
  height: 20px;
  margin-top: 10px;
}
.parameter-head span {
  width: 20%;
  float: left;
}
.parameter-head span:nth-child(3) {
  width: 48%;
  float: left;
  margin-left: 2%;
  margin-right: 2%;
}
/* 非首行样式 */
.parameter-checkbox {
  width: 5%;
  float: left;
  height: 20px;
  margin-top: 10px;
}
/* 表格中的文字样式 */
.content-parameter li > span {
  width: 20%;
  text-align: left;
  line-height: 40px;
  float: left;
}
.content-parameter li > input {
  margin-top: 8px;
}
.content-parameter .parameter-name {
  width: 20%;
  float: left;
}
.content-parameter li .parameter-value {
  width: 48%;
  float: left;
  margin-left: 2%;
  margin-right: 2%;
}
.content-parameter li .parameter-operating {
  font-size: 14px;
  cursor: pointer;
  color: #40A8FF;
}
/* 调试页面 */
.content-url {
  overflow: hidden;
  height: 35px;
  margin-bottom: 10px;
  position: relative;
}
.content-url > span {
  width: 80px;
  color: #fff;
  text-align: center;
  height: 100%;
  line-height: 35px;
  position: absolute;
  left: 0;
  top: 0;
}
.content-url > div {
  display: block;
  margin-left: 80px;
  margin-right: 88px;
  height: 35px;
}
.content-url > div input {
  color: #858585;
  background-color: #fff;
  border: 1px solid #d5d5d5;
  padding: 5px 4px;
  line-height: 1.2;
  font-size: 14px;
  font-family: inherit;
}
.content-url > button {
  position: absolute;
  right: 0;
  top: 0;
  height: 35px;
  background: #40A8FF;
  padding: 0 23px;
  border: 0;
  color: #fff;
}
.content-url > button:active {
  background-color: #1b6aaa !important;
  border-color: #428bca;
}
/* 接口详细信息列表 */
.swagger-content, .debugging-content {
  border-top: 1px solid #dbdbdb;
  padding: 20px 10px;
}
.content-list {
  text-align: left;
  margin: 0;
  padding: 15px;
  border: 1px solid #DDDDDD;
}
.content-list > li {
  list-style: none;
  text-align: left;
  border-bottom: 1px solid #DDDDDD;
  overflow: hidden;
}
.content-list > li > span {
  display: inline-block;
  width: 108px;
  padding: 10px 8px 8px;
}
.content-list > li > div {
  margin-left: 125px;
  padding: 10px;
}
.content-list > li > div > span {
  display: inline-block;
}
.content-list > li > div .request-table {
  padding: 8px;
}
.content-list > li > div .request-table > ul {
  border: 1px solid #ddd;
  border-bottom: 0;
}
.content-list > li > span:nth-child(1) {
  font-weight: 700;
  border-right: 1px solid #ddd;
  margin-bottom: -1000px;
  padding-bottom: 1000px;
  min-height: 26px;
  vertical-align: top;
  float: left;
}
/*  接口详细内容 */
.swagger-main {
  -webkit-box-shadow: 1px 1px 5px #f3f4ef;
          box-shadow: 1px 1px 5px #f3f4ef;
  border: 1px solid #F3F4EF;
  margin-left: 43%;
  margin-right: 15px;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.switch {
  margin-top: 15px;
  text-align: left;
  padding-left: 20px;
  font-size: 0;
}
.switch span {
  font-size: 14px;
  display: inline-block;
  padding: 10px 15px;
  border: 1px solid #EBEBEB;
  position: relative;
  top: 1px;
}
.switch span:nth-child(1) {
  position: relative;
  right: -1px;
}
.switch span.active {
  -webkit-box-shadow: 0 3px #89BF05 inset;
          box-shadow: 0 3px #89BF05 inset;
  color: #89BF05;
  border-bottom: 1px solid transparent;
  border-bottom: 1px solid #FFFFFF;
}


.slide-fade-enter-active, .slide-fade-leave-active {
  -webkit-transition: all .5s;
  transition: all .5s;
}
.slide-fade-enter, .slide-fade-leave-to {
  opacity: 0;
}

/* 请求参数表格 */
.table-tr {
  border-bottom: 1px solid #ddd;
  overflow: hidden;
  word-wrap: break-word;
}
.table-head {
  font-size: 16px;
  font-weight: 700;
  background-color: #F8F8F8;
}
.table-head .table-td {
  padding: 8px 4px;
}
.table-td {
  border-right: 1px solid #ddd;
  width: 15%;
  min-height: 18px;
  float: left;
  padding: 8px 4px;
  /*padding-bottom: 1000px;*/
  /*margin-bottom: -1000px;*/
  text-align: left;
}
.table-td:last-child {
  border-right: 0;
}
.table-tr .fontRight {
  text-align: right;
}
.fontColor {
  cursor: pointer;
  color: #4395ff;
}
